.container img {
    border-radius: 35px;
    width: 70px;
    height: 70px;
}

.container{
    display: block;

}
.active {
    font-weight: bold;
}
.paginators {
    display: flex;
    align-items: center;
    padding-top: 10px;
}
.paginators span {

    display: flex;
    align-items: center;
    justify-content: center;
    width: 20px;
    border: 2px purple solid;
    border-radius: 3px;
    margin-left: 10px;
    height: 25px;
}
.paginators button {
    display: block;
    margin-left: 10px;
    border-radius: 3px;
    border: 1px hotpink solid;
    height: 25px;
}