.containerTwo{
    width: 80%;
    display: flex;
    padding: 20px;
    margin: 10px auto;
    align-items: center;

}
.follow{
    display: grid;
    grid-template-areas: "image" "button";

}

.info{
    width: 80%;
    height: 80px;
    display: grid;
    grid-template-areas: "nameFollow citeCountry" "status citeCountry";
    grid-template-columns: 5fr 1fr;
    grid-template-rows: 1fr 1fr;
    margin-left: 10px;
    border: black solid 1px;
    border-radius: 10px;
    padding: 10px;
}
.nameFollow{
    display: flex;
    grid-area: nameFollow;
}
.status{
    display: flex;
    align-items: end;
    grid-area: status;
}
.citeCountry{
    display: flex;
    grid-area: citeCountry;
    justify-content: center;
    align-items: center;

}